.templTable {
  position: absolute;
  top: 35%;
  left: 43.5%;
  width: 500px;
  height: 500px;
  margin: -200px -200px -100px -150px;
  overflow-y: auto;
}

.templTable tbody > tr > td > p {
  margin: 0px;
  padding: 0px;
}

.copyRight {
  margin: 0px;
  padding: 0px;
  text-align: right;
  font-size: 0.5rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

